.homeContainer {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.LappaLogo {
    display: flex;
    margin-top: 20vh;
    width: 1vh;
    max-width: 70vw;
    transition: width 2s;
}
.btnTransportista {
    display: flex;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    bottom: 5vw;
    border: none;
    padding: 0px 2vh;
}
.btnSecundario {
    display: none;
}
a:hover {
    color: orangered;
}


@keyframes quieroIngresarAnimation {
        from {
            width: 70vw;
        }
        to {width: 1vw;}
}
@keyframes inicio {
        from {
            width: 1vw;
        }
        to {width: 70vw;}
}
@keyframes haciaArriba {
    from {
        height: 70vh;
    }
    to {height: 300vh;}
}@keyframes haciaAbajo {
    from {
        height: 300vh;
    }
    to {height: 70vh;}
}